import React from 'react';
import Tilt from 'react-tilt';
import './logo.css';
import brain from './brain.png';


const Logo = () => {
    return (
        <div className='m4 mt0' >

        <Tilt className="Tilt b2 shadow-2" options={{ max : 55 }} style={{ height: 150, width: 150 }} >
 <div className="Tilt-inner p3"> <img style={{paddingTop: '20px', width: '100px'}} src={brain} alt='brain' /> </div>
</Tilt>
        </div>
    )
}

export default Logo;