import React from 'react';
import './FaceRecognition.css';

const FaceRecognition = ({imageUrl, box}) => {
    return (
        <div className='center ma'>
        <div className="absolute mt2">

        <img id='inputimage' src={imageUrl} alt="the face " width='500px' height='auto'/>
        {box.map(boxes => {
            return (
                <div style={{top: boxes.topRow, right: boxes.rightCol, bottom: boxes.bottomRow, left: boxes.leftCol}} className='bounding-box'></div>
            )
        })}
        </div>
        </div>
    )
}

export default FaceRecognition;